<template>
  <div class="col-9 pt-3 pb-0 bg-muted d-flex flex-column">
    <loading-overlay :loading="loadingOverlay" class="flex-fill overflow-h">
    <div class="px-4 row d-block">
      <div v-if="admin.role == 'Admin'">
        <h5>
          <strong>Full Access Admins</strong>
        </h5>
        <div class="d-flex">
          <div
            class="card border-0 br bg-white mr-2 mb-2 px-2 py-3 justify-content-center text-primary font-weight-bold"
            @click="addAdminAndAgent('Full',fullAccessList)"
          >+ Add</div>
          <div
            v-for="a in fullAccessList"
            :key="a.id"
            class="card border-0 br bg-white mr-2 mb-2 px-2 py-3"
            @click.stop="editAdminAndAgent('Full',a,fullAccessList)"
          >
            <div class="d-flex align-items-center">
              <avatar :src="userIcon" :size="40" class="custom-avatar"></avatar>
              <div class="ml-2 d-flex flex-column">
                <span>{{ a.name }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="hasChannel && admin.role == 'Admin'" class="mt-2">
        <h5>
          <strong>Channel Admins</strong>
        </h5>
        <div class="d-flex">
          <div
            class="card border-0 br bg-white mr-2 mb-2 px-2 py-3 justify-content-center text-primary font-weight-bold"
            @click="addAdminAndAgent('Team',teamAllAdminList)"
          >+ Add</div>
          <div
            v-for="a in teamAllAdminList"
            :key="a.id"
            class="card border-0 br bg-white mr-2 mb-2 px-2 py-3"
            @click.stop="editAdminAndAgent('Team', a,teamAllAdminList)"
          >
            <div class="d-flex">
              <avatar :src="userIcon" :size="40" class="custom-avatar"></avatar>
              <div class="ml-2 d-flex flex-column">
                <span>{{ a.name }}</span>
                <span v-for="(c, i) in a.channels" :key="i">
                  <small class="text-primary font-weight-bold">
                    {{
                    c
                    }}
                  </small>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="mt-2">
        <h5>
          <strong>Agents</strong>
        </h5>

        <div class="mb-2">
          <button
            class="mb-2 btn btn-primary float-right"
            @click="addAdminAndAgent('Agent',getFilterAgents)"
          >Add Agent</button>
          <template v-if="hasChannel">
            <label class="text-muted">Filter By Channel</label>
            <select class="form-control br" style="width: 250px" v-model="filterChannel">
              <option v-if="admin.role == 'Admin'">All</option>
              <option v-for="c in channelList" :key="c" v-bind:value="c.id">{{c.name}}</option>
            </select>
          </template>
        </div>
        <div style="clear: both;">
          <div
            v-for="a in getFilterAgents"
            :key="a.key"
            class="card pointer border-0 br bg-white mr-2 mb-2 px-2 py-3 d-flex justify-content-between flex-row align-items-center"
            @click.stop="editAdminAndAgent('Agent',a,getFilterAgents)"
          >
            <div class="d-flex align-items-center">
              <avatar :src="userIcon" :size="40" class="custom-avatar"></avatar>
              <span>{{ a.name }}</span>
            </div>
            <div v-if="hasChannel" class="d-flex flex-column align-items-center">
              <small class="font-weight-bold text-primary" v-for="c in a.channels" :key="c">{{c}}</small>
            </div>
          </div>
        </div>
      </div>
    </div>
    </loading-overlay>
    <user-modal @updateUser="getPageData()" @updateFilterUser="updateFilterUser" />
  </div>
</template>
<script>
import axios from "axios";
import { mapGetters } from "vuex";
import UserModal from "./User/UserModal";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";

export default {
  components: {
    UserModal,
    LoadingOverlay
  },
  data() {
    return {
      fullAccessList: [],
      teamAllAdminList: [],
      teamAgentList: [],
      filterAgentList: [],
      channelList: [],
      channelNameList: [],
      channelId: "",
      fullname: "",
      username: "",
      password: "",
      channel: [],
      filterChannel: "All",
      role: "",
      show: "Admin",
      id: "",
      status: false,
      loadingOverlay:false
    };
  },

  methods: {
    addAdminAndAgent(status, userList) {
      this.$modal.show("adminAndAgent", {
        status: status,
        exist: "Add",
        userList: userList,
        user: userList,
        channel: this.channelNameList
      });
    },
    editAdminAndAgent(status, user, userList) {
      this.$modal.show("adminAndAgent", {
        status: status,
        exist: "Edit",
        user: user,
        userList: userList,
        channel: this.channelNameList
      });
    },

    async getPageData() {
      try {
        this.loadingOverlay=true;
        // let loader = this.$loading.show({
        //   loader: "dots"
        // });
        const res = await axios
          .get("settings/user")
          .then(res => {
            this.channelList = res.data.channels;
            this.fullAccessList = res.data.fullAccessAdmins;
            this.teamAllAdminList = res.data.teamAdmins;
            this.filterAgentList = res.data.agents;
            this.channelNameList = res.data.channels.map(x => x.name);
            console.log("filteragent", this.filterAgentList);
          })
          .finally(() => {
            //loader.hide();
            this.loadingOverlay=false;
          });
      } catch {
        return null;
      }
    },
    updateFilterUser(data, obj) {
      console.log(obj);
      this.filterAgentList.unshift({
        id: data,
        name: obj.username,
        role: obj.role,
        password: obj.password,
        channels: obj.channel
      });
    },

    async update() {
      if (this.filterChannel == "All") {
        this.getPageData();
      } else {
        let obj = {
          channel: this.filterChannel
        };
        let loader = this.$loading.show({
          loader: "dots"
        });
        const res = await axios
          .post("admin/getFiltersAgents", obj)
          .then(res => {
            this.filterAgentList = res.data;
            console.log("filter", this.filterAgentList);
          })
          .finally(() => {
            loader.hide();
          });
      }
    },
    getName(list) {
      var name = "";
      for (let l of list) {
        name += l + ", ";
      }
      return name;
    },
    getAdminsUnderChannel: (admins, channelId) =>
      admins.find(x => x.Key === channelId).List
  },
  async mounted() {
    await this.getPageData();
  },
  computed: {
    ...mapGetters(["admin"]),
    userIcon: () => require("../../assets/user.png"),
    getFilterAgents() {
      return this.filterAgentList;
    },
    hasChannel() {
      return this.channelList.length > 1;
    }
  },
  watch: {
    filterChannel() {
      this.update();
    }
  }
};
</script>
<style lang="scss">
.card span {
  font-weight: 600;
  letter-spacing: 0.5px;
}
.button {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 30%;
  padding-top: 20px;
  padding-left: 80%;
}
.header-bar {
  padding: 5px 10px;
  white-space: nowrap;
  overflow: auto;
  align-items: center;
  background-color: white;
  border-radius: 18px;
  box-shadow: 0 11.5px 6.5px -3px rgba(0, 0, 0, 0.024),
    0 18.1px 21.9px -3px rgba(0, 0, 0, 0.035),
    0 28px 98px -3px rgba(0, 0, 0, 0.17);
}

.form {
  padding: 30px;
  margin: 10px;
}
.textbox {
  margin-bottom: 10px;
  display: block;
}
label {
  margin-bottom: 0px;
  color: #385898;
  font-weight: bold;
  font-size: 15px;
  text-align: left;
}
</style>
