<template>
  <modal name="adminAndAgent" height="auto" width="400" @before-open="beforeOpen" @opened="focus">
    <loading-overlay white :loading="loading">
      <div class="p-2">
        <template>
          <div class="form">
            <label>User Name</label>
            <input
              placeholder=" Enter User Name"
              type="text"
              v-model="username"
              ref="username"
              class="textbox form-control"
            />
            <label>Password</label>
            <input
              placeholder=" Enter Password"
              type="password"
              v-model="password"
              show-password
              class="textbox form-control"
            />
            <template v-if="status!='Full'">
              <label v-if="channelList.length>1">Channels</label>
              <v-select
                v-if="channelList.length>1"
                multiple
                v-model="channels"
                :options="channelList"
                class="style-chooser"
              />
            </template>

            <div class="d-flex flex-row-reverse justify-content-between align-items-center">
              <button v-if="editModal" class="btn btn-primary my-2 btn-sm" @click="update">Update</button>
              <a v-if="editModal" @click="remove" class="text-danger mr-2">
                <i class="lar la-trash-alt"></i> Delete
              </a>
              <button v-else class="btn btn-primary my-2 btn-sm" @click="add">Add</button>
            </div>
            <a v-if="exists" class="text-danger mr-2">
              <i class="las la-exclamation-circle"></i>User Already Exist
            </a>
          </div>
        </template>
      </div>
    </loading-overlay>
  </modal>
</template>
<script>
import axios from "axios";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
  components: {
    LoadingOverlay
  },
  data() {
    return {
      username: "",
      password: "",
      id: 0,
      user: {},
      userList: [],
      status: "",
      loading: false,
      channels: [],
      channelList: [],
      exist: ""
    };
  },
  methods: {
    resetModal() {
      this.fullname = "";
      this.username = "";
      this.password = "";
      this.user = {};
      this.userList = [];
    },
    beforeOpen(event) {
      this.resetModal();
      let { status, editModal, user, channel, userList, exist } = event.params;
      this.status = status;
      this.exist = exist;
      this.channelList = channel;
      if (this.channelList.length == 1) {
        this.channels = channel;
      }
      this.userList = userList;
      if (editModal) this.editModal = editModal;
      if (user) {
        this.user = user;
        this.username = user.name;
        this.password = user.password;
        this.channels = user.channels;
        this.id = user.id;
      }
    },
    focus() {
      this.$refs.username.focus();
    },
    update() {
      if (!this.username) return;
      if (this.selfExist) {
        if (this.passwordExists && this.channelExists) return;
      } else {
        if (this.exists) {
          return;
        }
      }
      // this.loading = true;

      let obj = {
        id: this.id,
        username: this.username,
        password: this.password,
        role: this.status,
        channel: this.channels
      };
      axios
        .post("admin/upDateAdminsAndAgents", obj)
        .then(res => {
          if (res.status == 200) {
            this.$emit("updateUser");
          }
        })
        .finally(() => {
          // this.loading = false;
          this.$modal.hide("adminAndAgent");
        });

      this.$emit("updateUser", obj);
    },
    remove() {
      let obj = {
        id: this.id
      };
      // this.loading = true;
      axios
        .post("admin/removeAdminsAndAgents", obj)
        .then(res => {
          if (res.status == 200) {
            this.$emit("updateUser");
          }
        })
        .finally(() => {
          // this.loading = false;
          this.$modal.hide("adminAndAgent");
        });
    },
    add() {
      if (!this.username) return;
      if (this.exists) {
        return;
      }
      // console.log("add", this.channels);
      let obj = {
        username: this.username,
        password: this.password,
        role: this.status,
        channel: this.channels
      };
      axios
        .post("admin/upsertAdminsAndAgents", obj)
        .then(res => {
          if (res.status == 201) {
            if (obj.role == "Agent") {
              this.$emit("updateFilterUser", res.data, obj);
            } else {
              this.$emit("updateUser");
            }
          }
        })
        .finally(() => {
          // this.loading = false;
          this.$modal.hide("adminAndAgent");
        });
    }
  },
  computed: {
    selfExist() {
      return this.userList.map(x => x.name).includes(this.username);
    },
    exists() {
      if (this.exist == "Add") {
        return this.userList.map(x => x.name).includes(this.username);
      } else {
        return this.userList
          .filter(x => x.name != this.user.name)
          .map(x => x.name)
          .includes(this.username);
      }
    },
    passwordExists() {
      return this.user.password == this.password;
    },
    channelExists() {
      return this.user.channels.length == this.channels.length;
    },
    editModal() {
      return this.user.id;
    }
  }
};
</script>
